











import { Component, Vue, Watch } from 'vue-property-decorator'

/**
 * @title  
 * @desc  config  notification
 */
@Component({
  components: {
  },
  })
export default class GlobalExample extends Vue {
   config: any = {
      duration: 3000,
      maxCount: 2
   }

   info () {
     this.$notification.info('Config', 'this is a info message')
   }

   @Watch('config', {deep: true}) configChange (config: any) {
     this.$notification.config(config)
   }
}
