module.exports = "<template>\n<div>\n   <div class=\"my-3\">\n     <v-button @click=\"loading\">load</v-button>  &nbsp;\n     <v-button color=\"error\" @click=\"close\"></v-button>\n   </div>\n</div>\n</template>\n<script lang=\"ts\">\nimport { Component, Vue, Watch } from 'vue-property-decorator'\n\n/**\n * @title  \n * @desc duration0   Promise\n */\n@Component({\n  components: {\n  },\n  })\nexport default class AsyncExample extends Vue {\n   closeFn: Function | null = null\n\n   loading () {\n     this.closeFn = this.$message.loading('Action in progress...', 0)\n   }\n\n   close () {\n     if (this.closeFn) this.closeFn()\n   }\n}\n</script>\n"